import React, { useEffect, useState } from 'react';
import { Row, Col } from 'antd';
import { TableData } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import { getStudentGradeResult } from '../../../services/classCreditEnrollment';

const initColumns = [
  {
    title: 'Mã sinh viên',
    dataIndex: 'studentCode',
    key: 'studentCode',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'className',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Nơi sinh',
    dataIndex: 'pob',
    key: 'pob',
  },
  {
    title: 'Xếp loại',
    dataIndex: 'classification',
    key: 'classification',
  },
  {
    title: 'Số HP nợ',
    dataIndex: 'noOfDebtCredit',
    key: 'noOfDebtCredit',
  },
];

const toObject = (arr = [], key) => {
  return arr.reduce((obj, item) => {
    obj[item[key]] = item;
    return obj;
  }, {});
};

const TableListSummarizeSemester = ({ filterColTable, setTotalStudentSearch }) => {
  const [queryParams, setQueryParams] = useState({});
  const [dataTable, setDataTable] = useState([]);
  const [columns, setColumns] = useState([...initColumns]);
  const handleChangeQueryParams = () => {
    setQueryParams(getQueryParams());
  };

  useEffect(() => {
    handleChangeQueryParams();
  }, [getQueryParams()._time]);

  const createColTotalGrade = () => {
    return filterColTable.showGrade4
      ? {
          title: 'TBCHT4',
          dataIndex: 'total4',
          key: 'totalGrade',
        }
      : {
          title: 'TBCHC10',
          dataIndex: 'total10',
          key: 'totalGrade',
        };
  };

  const createColSubjectGrade = subject => {
    if (!filterColTable.showGradeAllTest) return [];

    const getSubjectGrade = (text, record) => {
      const subjectGrade = record.subjectWithGradePartsObj[subject.subjectId];
      if (!subjectGrade) {
        return '';
      }
      return subjectGrade.grade;
    };
    return [
      {
        title: `Thi`,
        key: `${subject.subjectId}-grade`,
        render: getSubjectGrade,
      },
    ];
  };

  const createColSubjectFinalGrade = subject => {
    const getSubjectFinalGrade = (text, record) => {
      const subjectGrade = record.subjectWithGradePartsObj[subject.subjectId];
      if (!subjectGrade) {
        return '';
      }
      return filterColTable.showGrade4 ? subjectGrade.finalGrade4 : subjectGrade.finalGrade10;
    };
    return [
      {
        title: `TBCMH`,
        key: `${subject.subjectId}-finalGrade`,
        render: getSubjectFinalGrade,
      },
    ];
  };

  const createChildrenGrade = subject => {
    const colGrade = createColSubjectGrade(subject);
    if (!filterColTable.showGradeParts) {
      return [...colGrade];
    }
    const colGradeParts = subject.gradeParts.map(part => {
      return {
        title: part.gradePartLabel,
        key: `${subject.subjectId}-${part.gradePartId}`,
        render: (text, record) => {
          if (!record.subjectWithGradePartsObj[subject.subjectId]) {
            return '';
          }
          if (!record.subjectWithGradePartsObj[subject.subjectId].gradePartsObj) {
            return '';
          }
          return record.subjectWithGradePartsObj[subject.subjectId].gradePartsObj[part.gradePartId]
            .grade;
        },
      };
    });
    return [...colGrade, ...colGradeParts];
  };

  useEffect(() => {
    if (!dataTable || dataTable.length === 0) return;
    const subjectWithGradePartsAll = dataTable.flatMap(
      ({ subjectWithGradeParts }) => subjectWithGradeParts
    );
    const subjectsObj = subjectWithGradePartsAll.reduce((obj, item) => {
      return { ...obj, [item.subjectId]: item };
    }, {});

    const extraCol = Object.values(subjectsObj).map(item => {
      return {
        title: item.subjectName,
        key: item.subjectName,
        children: [...createChildrenGrade(item), ...createColSubjectFinalGrade(item)],
      };
    });
    const colGradeTotal = createColTotalGrade();
    setColumns([...initColumns, colGradeTotal, ...extraCol]);
  }, [dataTable, filterColTable]);

  const getStudentGradeResultCustom = async params => {
    const res = await getStudentGradeResult(params);
    res.data.items = res.data.items.map(item => {
      item.subjectWithGradeParts = item.subjectWithGradeParts.map(subjectGrade => {
        subjectGrade.gradePartsObj = toObject(subjectGrade.gradeParts, 'gradePartId');
        return subjectGrade;
      });
      const subjectWithGradePartsObj = toObject(item.subjectWithGradeParts, 'subjectId');
      item.subjectWithGradePartsObj = subjectWithGradePartsObj;
      return item;
    });
    setTotalStudentSearch(res.data.totalResults);
    return res;
  };

  return (
    <Row>
      <Col>
        <div>
          <TableData
            columns={columns}
            scroll={{ x: 'true' }}
            allowGetData={!!queryParams.studyFieldId}
            paramSearch={queryParams}
            service={getStudentGradeResultCustom}
            dataTable={setDataTable}
          />
        </div>
      </Col>
    </Row>
  );
};

export default TableListSummarizeSemester;



