import React, { useState } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Icon } from 'antd';
import CartHeadAction from 'components/common/CartHeadAction';
import { BtnExport, BtnPrint } from 'components/common/button';
import FormInfoCommonSemester from './FormInfoCommonSemester';
import TableListSummarizeSemester from './TableListSummarizeSemester';
import './style.scss';

const ListSummarizePointsPage = () => {
  const [filterColTable, setFilterColTable] = useState({
    showGradeParts: false, // hien thi diem qua trinh
    showGradeAllTest: false, // hien thi tat ca diem thi
    showGrade4: false,
  });
  const [totalStudentSearch, setTotalStudentSearch] = useState(false);

  const handleSetFilterColTable = value => {
    setFilterColTable({ ...filterColTable, ...value });
  };
  return (
    <Card title="Kết quả học tập">
      <Row className="rowMargin">
        <Col span={24}>
          <CartHeadAction>
            <BtnPrint>
              <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" />
              In báo cáo
            </BtnPrint>
            <BtnExport>
              <Icon type="file-excel" theme="twoTone" twoToneColor="#00cc00" />
              Xuất Excel
            </BtnExport>
          </CartHeadAction>
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <FormInfoCommonSemester setFilterColTable={handleSetFilterColTable} />
        </Col>
      </Row>
      <Row>
        <Col span={24} style={{ textAlign: 'right' }}>
          {totalStudentSearch && (
            <p>
              Tổng số SV: <b>{totalStudentSearch}</b>
            </p>
          )}
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <TableListSummarizeSemester
            setTotalStudentSearch={setTotalStudentSearch}
            filterColTable={filterColTable}
          />
        </Col>
      </Row>
    </Card>
  );
};

export default props => (
  <Page {...props}>
    <ListSummarizePointsPage />
  </Page>
);



