import React, { useEffect } from 'react';
import { Row, Col, Form, Select, Checkbox } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { BtnClear, BtnSearch } from 'components/common/button';
import { requiredRule } from 'utils/validateRules';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import './style.scss';

const optionFieldDecorator = {
  trainTypeId: {
    rules: [requiredRule],
  },
  facultyId: {
    rules: [requiredRule],
  },
  courseId: {
    rules: [requiredRule],
  },
  studyFieldId: {
    rules: [requiredRule],
  },
  // semesterId: {
  //   rules: [requiredRule],
  // },
  // yearEduId: {
  //   rules: [requiredRule],
  // },
};
const { Option } = Select;
const FormInfoCommon = ({ form, setFilterColTable }) => {
  const { getFieldDecorator, setFieldsValue, resetFields, validateFields } = form;
  useEffect(() => {
    const params = getQueryParams();
    setFieldsValue(params);
  }, []);

  const handleSubmit = e => {
    e.preventDefault();
    validateFields((err, values) => {
      console.log(values);
      if (err) return;
      updateQueryParams(values);
    });
  };
  const handleClear = e => {
    e.preventDefault();
    resetFields();
  };

  const handleChangePointLadder = value => {
    setFilterColTable({ showGrade4: value === 4 });
  };

  const handleChangeShowGradeParts = value => {
    setFilterColTable({ showGradeParts: value.target.checked });
  };

  const handleChangeShowGradeAllTest = value => {
    setFilterColTable({ showGradeAllTest: value.target.checked });
  };

  return (
    <Form className="form-inline-label" layout="vertical">
      <Row gutter={24}>
        <FilterPathEducate optionFieldDecorator={optionFieldDecorator} form={form} />
        <YearSemesterInput optionFieldDecorator={optionFieldDecorator} span={5} form={form} />
        <Col span={4}>
          <Form.Item label="Lần thi">
            {getFieldDecorator('testNo', { initialValue: '', rules: [] })(
              <Select allowClear>
                {[1, 2, 3, 4].map(i => {
                  return <Option value={i}>{i}</Option>;
                })}
              </Select>
            )}
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="Thang điểm">
            <Select defaultValue={10} onChange={handleChangePointLadder}>
              <Option value={10}>Thang điểm 10</Option>
              <Option value={4}>Thang điểm 4</Option>
            </Select>
          </Form.Item>
        </Col>
        <Col span={4} style={{ textAlign: 'right' }}>
          <div className="btn-group">
            <BtnSearch onClick={handleSubmit} />
            <BtnClear onClick={handleClear} />
          </div>
        </Col>
      </Row>
      <Row gutter={24} className="rowMargin">
        <Col span={8}>
          <Checkbox onChange={handleChangeShowGradeParts} /> Hiển thị cột điểm quá trình
        </Col>
        <Col span={8}>
          <Checkbox onChange={handleChangeShowGradeAllTest} /> Hiển thị tất cả điểm thi
        </Col>
        <Col span={8}>
          <Form.Item className="form-item-checkbox" label="Không tính HP học lại và Cải thiện">
            {getFieldDecorator('isNoRestudy', {
              valuePropName: 'checked',
              initialValue: false,
            })(<Checkbox />)}
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
};

export default Form.create({ name: 'formSerchIndividual' })(FormInfoCommon);



